              gradientToColors: ['#A9A2F6'],
              inverseColors: true,
              opacityFrom: 1,
              opacityTo: 1,
              stops: [0, 100],
            },
          },
          stroke: {
            dashArray: 8,
          },
          labels: [''],
        },
      },
      renderComponent: true,
      token: '',
      floors: {},
      chosenUnit: null,
      completePercentage: 0,
      userUnitTypeId: 0,
      debt: '',
      state: '',
      progress: {},
      currentUser: null,
      ownerName: '',
      renterName: '',
      ownerMobile: '',
      renterMobile: '',
      initialBalance: '',
      footage: '',
      residentCount: '',
      required,
      mobile,
    }
  },
  mounted() {
    this.token = localStorage.getItem('token')
    this.getUnit()
  },
  methods: {
      separate() {
          this.initialBalance = separator(this.initialBalance)
      },
    meterageRegister() {
      managerService.unitStore({unit: [{
          unit_id: this.chosenUnit.unit_id,
          footage: this.footage,
          resident_count: this.residentCount
        }]}).then(res => {
          this.getUnit()
        this.$bvModal.hide('modal-meterage')
      })
    },
    registerDone() {
      this.$bvModal.hide('initial-balance')
      axios
          .post('//sm.oop.ir/api/v1/manager/dashboard/set-ready',
              {
                initial_balance: this.initialBalance
              },
              {
                headers: {
                  'Authorization': 'Bearer ' + this.token
                }
              })
          .then(response => {
            this.$router.push({ path: '/' })
          })
    },
    getUnit() {
      axios
          .get('//sm.oop.ir/api/v1/manager/unit', {
            headers: {
              'Authorization': 'Bearer ' + this.token
            }
          })
          .then(response => {
            this.supportTrackerRadialBar.series = [response.data.progress.percent]
            this.progress = response.data.progress
            this.floors = response.data.list.reduce((groups, item) => {
              const group = (groups[item.floor.floor_name] || []);
              group.push(item);
              groups[item.floor.floor_name] = group;
              return groups;
            }, {})
          })
          .finally(() => {
              }
          )
    },
    setModalInfo(modalType, unit, userType) {
      this.debt = null
      this.currentUser = null
      this.ownerName = null
      this.ownerMobile = null
      this.renterName = null
      this.renterMobile = null
      this.footage = unit.footage
      this.residentCount = unit.resident_count
      if (modalType === 'edit') {
        this.state = 'edit'
        axios
            .get('//sm.oop.ir/api/v1/manager/user-debt/user-unit-debt', {
              params: {
                user_id: userType === 1 ? unit.owner.user_id : unit.renter.user_id,
                unit_id: unit.unit_id,
                first_document: true,
              },
              headers: {
                'Authorization': 'Bearer ' + this.token
              },
            })
            .then(response => {
              this.debt = response.data.user_debt
              this.currentUser = response.data.list[0]
              if (userType === 1) {
                this.ownerName = response.data.list[0].full_name
                this.ownerMobile = response.data.list[0].mobile
              }
              if (userType === 2) {
                this.renterName = response.data.list[0].full_name
                this.renterMobile = response.data.list[0].mobile
              }
              // console.log(this.debt)
            })
            .finally(() => {
                }
            )
      } else {
        this.state = 'new'
      }
      this.chosenUnit = unit
      this.userUnitTypeId = userType
    },
    getUser(type) {
      if (type === 1) {
        axios.get('//sm.oop.ir/api/v1/manager/user/detail', {
          params: { mobile: this.ownerMobile },
          headers: {
            'Authorization': 'Bearer ' + this.token
          }
        })
            .then(response => {
              if (response.data.data) {
                this.currentUser = response.data.data
                this.ownerName = response.data.data.full_name
                // this.ownerMobile = response.data.data.mobile
                // this.chosenUnit.owner.user_id = response.data.data.user_id
              }
            })
      }
      if (type === 2) {
        axios.get('//sm.oop.ir/api/v1/manager/user/detail', {
          params: { mobile: this.renterMobile },
          headers: {
            'Authorization': 'Bearer ' + this.token
          }
        })
            .then(response => {
              if (response.data.data) {
                this.currentUser = response.data.data
                this.renterName = response.data.data.full_name
                // this.renterMobile = response.data.data.mobile
                // this.chosenUnit.renter.user_id = response.data.data.user_id
              }
            })
      }
    },
    registerUser(userType) {
      axios
          .post('//sm.oop.ir/api/v1/manager/user/init',
              {
                user_id: this.currentUser ? this.currentUser.user_id : '',
                user_unit_type_id: userType,
                unit_id: this.chosenUnit.unit_id,
                name: userType === '1' ? this.ownerName : this.renterName,
                mobile: userType === '1' ? this.ownerMobile : this.renterMobile,
                debt: this.debt
              },
              {
                headers: {
                  'Authorization': 'Bearer ' + this.token
                }
              })
          .then(response => {
            this.getUnit()
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'ثبت اطلاعات با موفقیت انجام شد',
                icon: 'EditIcon',
                variant: 'success',
              },
            })
            this.debt = 0
            this.$bvModal.hide('modal-owner')
            this.$bvModal.hide('modal-renter')
          })
          .finally(() => {
              }
          )
          .catch((error) => {
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'خطا در ثبت اطلاعات',
                icon: 'EditIcon',
                variant: 'error',
              },
            })
          })

    },
    cancel(userType) {
      if (userType === '1') {
        this.$bvModal.hide('modal-owner')
      }
      if (userType === '2') {
        this.$bvModal.hide('modal-renter')
      }
    },
    deleteUser(uuId) {
      axios
          .delete('//sm.oop.ir/api/v1/manager/user-unit/delete?user_unit_id='+uuId,
              {},
              {
                headers: {
                  'Authorization': 'Bearer ' + this.token
                }
              })
          .then(response => {
            this.getUnit()
            // this.getUser()
            this.$toast({
              component: ToastificationContent,
              props: {
                title: 'ثبت اطلاعات با موفقیت انجام شد',
                icon: 'EditIcon',
                variant: 'success',
              },
            })
          })
          .finally(() => {
              }
          )
    }
  },

}
</script>

